#london-map, #lagos-map {
  display: none;
}

#london-map.active, #lagos-map.active {
  display: block;
}

.tab {
  cursor: pointer;
  margin-right: 10px;
  display: inline-block;
}

.tab.active {
  color: #b3d5fc;
}

iframe {
  display: none;
}

iframe.active {
  display: block;
}

.card-container {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  display: flex;
  position: relative;
}

.card {
  color: #fff;
  opacity: .9;
  background-color: #3498db;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  width: 15%;
  height: 350px;
  transition: transform 1.3s, opacity .3s;
  display: flex;
  position: absolute;
}
